  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding: 0;
}
html{
  scroll-behavior: smooth;
  overflow-y: scroll;
}

section{
  position: relative;
  width:100%;
  height:auto;
  background:rgb(21, 180, 180);  /* fallback for old browsers */
  /* background: -webkit-linear-gradient(to left, #93EDC7, #1CD8D2);  Chrome 10-25, Safari 5.1-6 */
  /* background: linear-gradient(to left, #93EDC7, #1CD8D2); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  
  overflow: hidden;
  text-align: center;
  font-size: 30px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  
 
  /* justify-content: center; */
  border-bottom: 1px solid lightgray;
  
}
@media only screen and (max-width: 768px) {
  /* For desktop: */
  
  section{width: 100%;
    background: rgb(21, 180, 180);  /* fallback for old browsers */
    /* background: -webkit-linear-gradient(to left, #93EDC7, #1CD8D2);  Chrome 10-25, Safari 5.1-6 */
    /* background: linear-gradient(to left, #93EDC7, #1CD8D2); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    
    }
    
}

section .Wave{
  position:absolute;
  bottom:0;
  left: 0;
  width: 100%;
  height: 100px;
  background: url(https://i.imgur.com/WYK0WCc.png);
  background-size: 1000px 100px;
  display: flex;
  flex-wrap: wrap;

}

section .Wave.Wave1{
  -webkit-animation:animate 30s linear infinite;
          animation:animate 30s linear infinite;
  z-index: 1000;
  opacity: 1;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  bottom: 0;
  display: flex;
  flex-wrap: wrap;
}
section .Wave.Wave2{
  -webkit-animation:animate 15s linear infinite;
          animation:animate 15s linear infinite;
  z-index: 999;
  opacity: 0.5;
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  bottom: 10px;
  display: flex;
  flex-wrap: wrap;
}
section .Wave.Wave2{
  -webkit-animation:animate 30s linear infinite;
          animation:animate 30s linear infinite;
  z-index: 998;
  opacity: 0.2;
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
  bottom: 15px;
  display: flex;
  flex-wrap: wrap;
}

section .Wave.Wave4{
  -webkit-animation:animate 5s linear infinite;
          animation:animate 5s linear infinite;
  z-index: 997;
  opacity: 0.7;
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  bottom: 20px;
  display: flex;
  flex-wrap: wrap;
}

@-webkit-keyframes animate{
  0%
  {
background-position-x:0;

  }
  100%{
    background-position-x:1000px ;
  }
}

@keyframes animate{
  0%
  {
background-position-x:0;

  }
  100%{
    background-position-x:1000px ;
  }
}



